// ==========================================
// TYPES
// ==========================================

interface DashboardStats {
  totalTickets: number
  openTickets: number
  resolvedToday: number
  avgResponseTime: number
  aiResolutionRate: number
  customerSatisfaction: number
  pendingAIReview: number
  urgentTickets: number
}

interface RecentTicket {
  id: string
  number: string
  subject: string
  customerName: string
  priority: 'low' | 'medium' | 'high' | 'urgent' | 'critical'
  status: string
  aiConfidence: number
  createdAt: string
}

interface AIInsight {
  type: 'trend' | 'alert' | 'suggestion' | 'performance'
  title: string
  description: string
  action?: string
  actionLink?: string
  severity?: 'info' | 'warning' | 'success' | 'error'
}

